import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Chip } from '@material-ui/core';

const useStyles = makeStyles({
  card: {},
  disabledCard: {
    filter: 'blur(1.2px)',
    '-ms-filter': 'blur(1.2px)',
    '-webkit-filter': 'blur(1.2px)',
    'moz-filter': 'blur(1.2px)',
    'o-filter': 'blur(1.2px)'
  },
  chips: {
    marginTop: 16 + 'px'
  },
  chip: {
    marginRight: 8 + 'px',
    marginBottom: 8 + 'px',
  },
});

function renderCardActions(buttons) {
  return (
    <CardActions>
      { buttons && buttons.map((button, index) => {
        return (
          <Button key={index} size="small" color="primary" onClick={() => button.onClick }>
            { button.title }
          </Button>
        )
      })}
    </CardActions>
  );
}

function ImageMediaCard(props) {
  const classes = useStyles();
  const {
    disabled,
    title,
    body,
    imageHeight,
    image,
    tags,
    buttons,
    onCardClick
  } = props;

  const renderTags = () => {
    if (tags !== undefined) {
      return tags.map((tag, i) => {
        return <Chip key={i} label={tag} size="small" className={classes.chip} />;
      });
    }
  }

  return (
    <Card className={ disabled ? classes.disabledCard : classes.card }>
      <CardActionArea
        disabled={disabled}
        onClick={(event) => onCardClick(props)}>
        <CardMedia
          component="img"
          alt={title}
          height={imageHeight}
          image={image}
          title={title}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {body}
          </Typography>
          <Typography className={classes.chips} component="div">
          { renderTags() }
        </Typography>
        </CardContent>
      </CardActionArea>
      { buttons && renderCardActions(buttons) }
    </Card>
  );
}

ImageMediaCard.propTypes = {
  disabled: PropTypes.bool,
  title: PropTypes.string.isRequired,
  body: PropTypes.string.isRequired,
  imageHeight: PropTypes.number,
  image: PropTypes.string,
  buttons: PropTypes.array,
  onCardClick: PropTypes.func
};

export default ImageMediaCard;