import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router-dom';

import ImageMediaCard from './ImageMediaCard';

import * as Constant from '../constants';
import { Grid } from '@material-ui/core';

const styles = theme => ({
  gridItem: {
    maxWidth: 340 + 'px',
    minWidth: 340 + 'px'
  },
  titleLink: {
    color: Constant.COLOR.primaryColor,
    textDecoration: 'none',
  },
  paperInfo: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  chip: {
    margin: theme.spacing(1),
  },
});


function PostListItem(props) {
  const { item, history, classes } = props;  

  return (
    <Grid item xs={12} md={4} lg={4} className={classes.gridItem}>
      <ImageMediaCard
        image={item.image}
        imageHeight={180}
        title={item.name}
        body={item.description}
        tags={item.tags}
        onCardClick={() => {
          history.push(`/${item.category}/${item.slug}`, item)}
        }
      />
    </Grid>
  );

}

PostListItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired
};

const styledPage = withStyles(styles)(PostListItem);
export default withRouter(styledPage);