import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet'

import { withStyles } from '@material-ui/core/styles';

import FlatList from '../components/FlatList';
import Spinner from '../components/Spinner';
import PostListItem from '../components/PostListItem';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';
import { Grid } from '@material-ui/core';

import { Posts } from '../api';
import { APP_NAME, APP_DESCRIPTION } from '../constants';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
  pageContent: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  }
});

class PostList extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      posts: []
    }

  }
  
  componentWillMount() {
    this.getPosts(this.props.match.params.category);
  }

  componentWillReceiveProps(nextProps) {
    this.getPosts(nextProps.match.params.category);
  }

  componentWillUnmount() {
    this.posts.unsubscribe();
  }

  getPosts = (category) => {
    this.setState({ loading: true });
    this.posts = Posts.getByCategory(category)
                      .subscribe(posts => {
                        this.setState({
                          posts,
                          loading: false
                        });
                      });
  }

  render() {
    const { loading, posts } = this.state;
    const { classes, match } = this.props;
    var page = match.params.category ? '| ' + match.params.category.capitalize() : ''

    return (
      <div className={classes.root}>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{`${APP_NAME} ${page}`}</title>
          <meta name="description" content={`${APP_DESCRIPTION}`}/>
        </Helmet>
        <SideBar />
        <PageContent>
          <Grid
            container 
            justify="center"
            spacing={4}>
            <FlatList 
              items={posts}
              isLoading={loading}
              renderLoading={ <Spinner /> }
              renderEmpty={ () => <div>There are no posts in this category..</div>}
              renderItem={ post => <PostListItem item={post} key={post.id} /> }
            />
          </Grid>
        </PageContent>
      </div>
    );
  }
}

PostList.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PostList);
export default connect(mapStateToProps, {})(styledComponent);