import * as types from './types';
import firebase from '../firebase';

let db = firebase.firestore();

export function getOpenSource() {
  return (dispatch, getState) => {
    db.collection("opensource")
      .orderBy('createdAt', 'desc')
      .get()
      .then((querySnapshot) => {
      let items = [];
      querySnapshot.forEach((doc) => {
        items.push({...doc.data(), id: doc.id});
      });
      dispatch(updateOpenSource(items));
    });
  }
}

export function getOpenSourceById(id) {
  return (dispatch, getState) => {
    return db.doc(`opensource/${id}`).get();
  }
}

export function updateOpenSource(openSourceItems) {
  return {
    type: types.SET_OPENSOURCE,
    openSourceItems,
  };
}