import firebase from '../firebase';
import { docData, collectionData } from 'rxfire/firestore';
import { isEmpty } from '../utils/isEmpty';

const postsRef = firebase.firestore().collection('posts');

export function getByCategory(category) {
  var query = postsRef;
  query = query.where('published', '==', 'published');
  if (!isEmpty(category)) {
    query = query.where('category', '==', category);
  }
  query = query.orderBy('createdAt', 'desc');
  return collectionData(query, 'id');
}

export function getById(id) {
  var query = postsRef.doc(id);
  return docData(query, 'id');
}

export function getBySlug(slug) {
  var query = postsRef.where('slug', '==', slug);
  return docData(query, 'id');
}

export function create(location) {
  return postsRef.add(location);
}

export function updateById(id, location) {
  return postsRef.doc(id).set(location, { merge: true });
}

export function deleteById(id) {
  return postsRef.doc(id).delete();
}