import * as types from './types';
import firebase from '../firebase';

let db = firebase.firestore();

const collectionRef = "projects"

export function getPortfolio() {
  return (dispatch, getState) => {
    return db.collection(`${collectionRef}`)
      .orderBy('createdAt', 'desc')
      .get()
      .then((querySnapshot) => {
      let items = [];
      querySnapshot.forEach((doc) => {
        items.push({...doc.data(), id: doc.id});
      });
      dispatch(updatePortfolio(items));

      return items;
    });
  }
}

export function savePortfolioItem(id, item) {
  return (dispatch, getState) => {
    return db.doc(`${collectionRef}/${id}`)
            .set(item)
            .then(function () {
                console.log("Document successfully written!");
                return true;
              })
              .catch(function (error) {
                console.error("Error writing document: ", error);
                return false;
              });
  }
}

export function getPortfolioById(id) {
  return (dispatch, getState) => {
    return db.doc(`${collectionRef}/${id}`).get();
  }
}

export function updatePortfolio(portfolioItems) {
  return {
    type: types.SET_PORTFOLIO,
    portfolioItems,
  };
}