import React from 'react';
import { connect } from 'react-redux';
import { getOpenSourceById } from '../actions';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import _ from 'lodash';
import ReactMarkdown from 'react-markdown';

import Spinner from '../components/Spinner';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';
import Breadcrumbs from '../components/Breadcrumbs';

import { Posts } from '../api';
import { isEmpty } from '../utils/isEmpty';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    maxWidth: 1400 + 'px',
    margin: '0 auto',
  },
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
});

class PostView extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      slug: props.match.params.slug || null,
      post: props.history.location.state || {}
    }
  }
  
  componentDidMount() {
    this.refresh();
  }

  refresh = () => {
    const { slug, post } = this.state;

    if (isEmpty(post)) {
      this.setState({ loading: true });
    }

    this.post = Posts.getBySlug(slug)
      .subscribe(post => {
        this.setState({ post, loading: false });
      });
  }

  componentWillUnmount() {
    this.post.unsubscribe();
  }

  render() {
    const { post } = this.state;
    const { classes } = this.props;

    if (_.isEmpty(post)) {
      return <Spinner />;
    } else {
      return (
        <div className={classes.root}>
          <SideBar />
          <PageContent>
            <Paper className={classes.paper} elevation={1}>
              <Breadcrumbs 
                name={post.name} 
                parentName={`${post.category.capitalize()}`}
                parentLink={`/${post.category}`} />
              <Typography variant="h4" component="h4">
                {post.name}
              </Typography>
              <Typography variant="h6" component="h6">
                {post.description}
              </Typography>
              <img 
                src={post.image} 
                style={{ width: 100 + '%'}} 
                title={post.name} 
                alt={''} />
              <ReactMarkdown source={post.body} />
            </Paper>
          </PageContent>
        </div>
      );
    }
  }

}

PostView.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PostView);
export default connect(mapStateToProps, { getOpenSourceById })(styledComponent);